import React, {Component} from 'react';
import axios from 'axios';

class ManualInput extends Component {
    render() {
        return(
            <div className="manual-inputwrap">
                <p className="manual-num-title">ÏàòÎèô Ï∂îÏ≤®</p>
                <p className="manual-num-info">1~ 45 Ï§ë ÏõêÌïòÎäî Ïà´Ïûê 6ÏûêÎ¶¨Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.<br />(Ïà´ÏûêÎ•º Í≥µÎ∞±ÏúºÎ°ú Íµ¨Î∂Ñ Ìï¥ Ï£ºÏÑ∏Ïöî) </p>
                
                
                <input className ="manual-num" type="text" placeholder="6ÏûêÎ¶¨Ïà´Ïûê ÏûÖÎ†•" name="manualNum" />
                <button className ="manual-result-btn" onClick = {function(e) {
                    // ÏàòÎèô Ïà´Ïûê ÏûÖÎ†• Îêú Î∂ÄÎ∂Ñ
                    const manualNum = document.querySelector('.manual-num');
                    //ÏàòÎèô Ï∂îÏ≤® Î≤ÑÌäº
                    const warningSixNum = document.querySelector('.warning-sixnum');
                    console.log('manualNum writed');
                    // inputÏúºÎ°ú Î∞õÏïÑÏò§Î©¥ newManualNumÏù¥ string ÌÉÄÏûÖ.
                    let newManualNum = manualNum.value;
                    let manualNumList = [];
                    //Í≥µÎ∞±ÏùÑ Í∏∞Ï§ÄÏúºÎ°ú ÏûÖÎ†• Î∞õÏùÄ Ïà´ÏûêÎ•º Íµ¨Î∂ÑÌïòÏó¨ manualNumListÏóê ÎÑ£Í∏∞
                    manualNumList = newManualNum.split(" ")
                    
                    for (var i = 0; i < manualNumList.length; i++) {
                      //Î∂àÌïÑÏöîÌïú Í≥µÎ∞± Ï†úÍ±∞
                      if (manualNumList[i] === "") {
                        manualNumList.splice(i, 1);
                        i--;
                      } else {
                        manualNumList[i] = parseInt(manualNumList[i])
                      }
                    }
                  
                    // ÏûÖÎ†• Î∞õÏùÄ ÏàòÎèô Ïà´ÏûêÍ∞Ä 6ÏûêÎ¶¨ Ïùº Ïãú Î∞±Ïóê form ÌÉúÍ∑∏ ÏÉùÏÑ±ÌïòÏó¨ Ï†ÑÏÜ°.
                    // ÏûÖÎ†•Î∞õÏùÄ Í∞íÏù¥ Ïà´ÏûêÍ∞Ä ÏïÑÎãàÍ±∞ÎÇò, 6ÏûêÎ¶¨Í∞Ä ÏïÑÎãàÍ±∞ÎÇò, 6ÏûêÎ¶¨ Ïàò Ï§ë  Ï§ëÎ≥µÎêòÎäî Í∞íÏù¥ ÏûàÏùÑ Ïãú Í≤ΩÍ≥† Ï∂úÎ†•
                  
                    // ÏûÖÎ†•Î∞õÏùÄ Î∞∞Ïó¥Ïùò ÏöîÏÜå Ï§ë Ïà´ÏûêÎ°ú Î≥ÄÌôòÎêòÏßÄ ÏïäÏùÄ Í∞íÏù¥ ÏûàÏúºÎ©¥ False Î∞òÌôò
                    const isNumber = (item) => !(Number.isNaN(item))
                  
                    // ÏûÖÎ†•Î∞õÏùÄ Î∞∞Ïó¥Ïùò ÏöîÏÜå Ï§ë Ï§ëÎ≥µÎêòÎäî Í∞íÏù¥ ÏûàÎã§Î©¥ False Î∞òÌôò
                    const isDiffer = manualNumList.length === new Set(manualNumList).size
                  
                    // ÏûÖÎ†•Î∞õÏùÄ Ïà´Ïûê Ï§ë 1~45Ïùò Î≤îÏúÑ Î∞ñÏùò Ïà´ÏûêÍ∞Ä ÏûàÏùÑ Í≤ΩÏö∞ False Î∞òÌôò
                    const isInRange = (item) => item >= 1 && item <= 45
                  
                    // ÏïûÏÑú Ï†ÅÏùÄ Ï°∞Í±¥ÏùÑ Î™®Îëê ÎßåÏ°±Ìï† Ïãú Îç∞Ïù¥ÌÑ∞ Ï†ÑÏÜ°
                    if ((manualNumList.length === 6) && (manualNumList.every(isNumber)) && isDiffer && manualNumList.every(isInRange)) {
                      console.log(manualNumList)
                      axios.post('/lotto', {
                        numbers: manualNumList
                      }).then(function(response) {
                          console.log('data posted')
                      }).catch(error=>{
                          console.log(error);
                          throw new Error(error);
                      }).then(function(){
                          window.location.pathname = "/showResult"
                      });
                    }
                    else {
                      warningSixNum.innerText = '1 ~ 45 Ï§ë ÏÑúÎ°ú Îã§Î•∏ 6Í∞úÏùò Ïà´ÏûêÎ•º ÏûÖÎ†• Ìï¥ Ï£ºÏÑ∏Ïöî ! ';
                    }
                }.bind()}>ÏàòÎèôÏ∂îÏ≤®</button>
                <p className="warning-sixnum"></p>
            </div>
        );
    }
}

export default ManualInput;