import React, { Component } from 'react';
class Table extends Component {
    render() {
        let headers=[];
        this.props.content.col.forEach(element => {
            let th = <th key={element}>{element}</th>
            headers.push(th)
        });
        // tdÏûëÏÑ±
        // Îû≠ÌÇπ ÏõêÎ≥∏ÏùÑ Î™®Îëê ÌëúÏãú
        let wholeRow = []; 
        // ÌëúÏùò td Î∂ÄÎ∂Ñ Ï†ÑÏ≤¥ ÏóòÎ¶¨Î®ºÌä∏Î•º Îã¥ÏùÑ Î∞∞Ïó¥ ÏÑ†Ïñ∏
        for(let i=0; i<this.props.content.totalRanking.length; i++){
            //tableÏóê ÏùºÎã® Ìïú ÌñâÏùÑ Ï∂îÍ∞Ä            
            let rowElem = [];
            // Ìïú ÌñâÏóê Îì§Ïñ¥Í∞à ÏóòÎ¶¨Î®ºÌä∏Î•º Îã¥ÏùÑ Î∞∞Ïó¥ ÏÑ†Ïñ∏ 
            //tableHeader Í∏∏Ïù¥ÎßåÌÅº ÏàúÌöåÌïòÎ©¥ÏÑú Îß§Ïπ≠ÎêòÎäî Îç∞Ïù¥ÌÑ∞ Í∞ñÍ≥†Ïò§Í∏∞.
            for(let j=0; j<this.props.content.col.length; j++) {
                // td ÏóòÎ¶¨Î®ºÌä∏Î•º ÏÑ†Ïñ∏ÌïòÏó¨ rowElemÏóê ÌïòÎÇòÏî© push
                let tabCell = <td key={this.props.content.col[j]}>{this.props.content.totalRanking[i][this.props.content.col[j]]}</td>;
                rowElem.push(tabCell);
                // Í≤∞Í≥º, rowElem = [<td></td>,...]
            }
            const row = 
            <tr key={i}>
                {rowElem}
            </tr>;
            wholeRow.push(row);
        }
        return(
            <div className="rank_table_here">
                <table id="rankTable">
                    <thead>
                        <tr>{headers}</tr>
                    </thead>
                    <tbody>
                        {wholeRow}
                    </tbody>
                    
                </table>
            </div>
        );
    }
}

export default Table;