import React, { Component } from 'react';
import axios from 'axios';

function makeRandomNumber(){
    let randomNumber = [];

    for(let i = 0; i<6; i++){
        let r = String(Math.floor(Math.random()*44+1));
        randomNumber[i] = r;
    }
    let numbers = new Set(randomNumber)
    //Ï§ëÎ≥µ Îêú Í∞íÏù¥ ÏûàÏùÑ Ïãú Îã§Ïãú Ï∂îÏ≤®.
    if (randomNumber.length !== numbers.size){
        randomNumber = makeRandomNumber()
    }
    return randomNumber;
}

class AutoInput extends Component {
    constructor(props){
        super(props);
        this.state = {
            randNum: ''
        };
    }
    render() {
        return(
            <div className="auto-inputwrap">
                <p className="auto-num-title">ÏûêÎèô Ï∂îÏ≤®</p>
                <p className="auto-num-info">'ÏûêÎèôÏ∂îÏ≤® Î≤àÌò∏ ÏÉùÏÑ±' Î≤ÑÌäºÏùÑ ÎàÑÎ•¥Î©¥<br /> 6ÏûêÎ¶¨ Î≤àÌò∏Í∞Ä ÏÉùÏÑ±Îê©ÎãàÎã§.<br />ÏõêÌïòÎäî ÎßåÌÅº ÏûêÎèô ÏÉùÏÑ± Í∞ÄÎä• Ìï©ÎãàÎã§. </p>
                <button className="auto-num-make" onClick = {function(e) {
                    this.setState({randNum: String(makeRandomNumber())})
                }.bind(this)}>ÏûêÎèô Î≤àÌò∏ ÏÉùÏÑ±</button>
                <p className="auto-text-field">{this.state.randNum}</p> 
                <button className="auto-result-btn" onClick = {function(e) {
                    const autoNumList = [];
                    const randomNumberArray = this.state.randNum;
                    const randomNumbersSplit = randomNumberArray.split(',');
                    console.log(randomNumbersSplit)
                    for (let i of randomNumbersSplit){
                        autoNumList.push(parseInt(i));
                    }
                    console.log(typeof(autoNumList))
                
                    // submit data
                    axios.post('/lotto', {
                        numbers: autoNumList
                    }).then(function(response) {
                        console.log('data posted')
                    }).catch(error=>{
                        console.log(error);
                        throw new Error(error);
                    }).then(function(){
                        window.location.pathname = "/showResult"
                    });
                }.bind(this)}>ÏûêÎèôÏ∂îÏ≤®</button>
            </div>
        )
    }
}

export default AutoInput;