import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class MainRank extends Component {
    
    render() {
        let headers=[];
        this.props.content.col.forEach(element => {
            let th = <th key={element}>{element}</th>
            headers.push(th)
        });
        // tdÏûëÏÑ±
        // Î©îÏù∏ ÌôîÎ©¥Ïùò Îû≠ÌÇπÏùÄ ÏõêÎ≥∏ Îû≠ÌÇπ Ï§ë ÏÉÅÏúÑ 5ÏúÑÎßåÏùÑ ÌëúÏãúÌïòÎèÑÎ°ù Ìï®
        let wholeRow = []; 
        // ÌëúÏùò td Î∂ÄÎ∂Ñ Ï†ÑÏ≤¥ ÏóòÎ¶¨Î®ºÌä∏Î•º Îã¥ÏùÑ Î∞∞Ïó¥ ÏÑ†Ïñ∏
        for(let i=0; i<this.props.content.totalRanking.length && i < 5; i++){
            //tableÏóê ÏùºÎã® Ìïú ÌñâÏùÑ Ï∂îÍ∞Ä            
            let rowElem = [];
            // Ìïú ÌñâÏóê Îì§Ïñ¥Í∞à ÏóòÎ¶¨Î®ºÌä∏Î•º Îã¥ÏùÑ Î∞∞Ïó¥ ÏÑ†Ïñ∏ 
            //tableHeader Í∏∏Ïù¥ÎßåÌÅº ÏàúÌöåÌïòÎ©¥ÏÑú Îß§Ïπ≠ÎêòÎäî Îç∞Ïù¥ÌÑ∞ Í∞ñÍ≥†Ïò§Í∏∞.
            for(let j=0; j<this.props.content.col.length; j++) {
                // td ÏóòÎ¶¨Î®ºÌä∏Î•º ÏÑ†Ïñ∏ÌïòÏó¨ rowElemÏóê ÌïòÎÇòÏî© push
                let tabCell = <td key={this.props.content.col[j]}>{this.props.content.totalRanking[i][this.props.content.col[j]]}</td>;
                rowElem.push(tabCell);
                // Í≤∞Í≥º, rowElem = [<td></td>,...]
            }
            const row = 
            <tr key={i}>
                {rowElem}
            </tr>;
            wholeRow.push(row);
        }
        
        console.log('Rank rendered');
        return(
            <div className="rank_wrap">
                <p className="rankmain">Î°úÎòê ÎãπÏ≤® Îû≠ÌÇπ Î≥¥Í∏∞</p>
                <div className="rank_table_here">
                    <table id="rankTable">
                        <thead>
                            <tr>{headers}</tr>
                        </thead>
                        <tbody>
                            {wholeRow}
                        </tbody>
                        
                    </table>
                </div><br />
                <Link to='showTotalRank'>Îû≠ÌÇπ Îçî Î≥¥Í∏∞</Link>
            </div>
        );
    }
}

export default MainRank;